<template>
  <div class="container">
    <div class="login">
      <div class="title">BMS</div>
      <div class="row">
        <label for="user_name">用户名</label>
        <input v-model="username" type="text" id="user_name">
      </div>
      <div class="row">
        <label for="user_pwd">密码</label>
        <input v-model="password" type="password" id="user_pwd">
      </div>
      <div class="row">
        <div class="bottom">
          <div><input type="checkbox" id="rememberme" value="forever"></div>
          <div><label for="rememberme">记住我的登录信息</label></div>
          <div class="login-btn" @click="login"><button>登录</button></div>
        </div>
      </div>
      <div class="loading">{{logding}}</div>
    </div>
  </div>
</template>

<script>
import { Login } from '@/api/api'

export default {
  data () {
    return {
      username: '',
      password: '',
      logding: ''
    }
  },
  methods: {
    login () {
      this.logding = '登录中. . .'
      Login(this.username, this.password).then(response => {
        if (response.data.code === 0) {
          this.logding = '登录成功'
          localStorage.setItem('token', response.data.data.token)
          this.$router.push('/admin')
        } else {
          this.logding = '登陆失败'
        }
      }).catch(err => {
        this.logding = '登陆失败'
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.container {
  display: flex;
  width: 100%;
  margin-top: 10%;
  justify-content: center; /* flex 属性， 水平居中 */
}
.login {
  /* width: 350px; */
  height: 300px;
}
.title {
  display: flex;
  justify-content: center; /* flex 属性， 水平居中 */
  margin-bottom: 40px;
  font-size: 30px;
  font-weight: bold;
  color: #FF6A00;

}
.row {
  margin-bottom: 20px;
  /* margin-left: 30px;
  margin-right: 30px; */
  color: #444;
}
.row label {
  display: block;
  font-size: 16px;
  margin-bottom: 10px;
}
.row input {
  width: 280px;
  height: 42px;
  font-size: 24px;
  text-indent: 4px;
  line-height: 2.33;
  border-radius: 4px;
  border: 1px solid #7e8993;
}
input[type=text]:focus,input[type=password]:focus {
  border: 1px solid #FF6A00;
  outline: none;
}
input[type=text]:-webkit-autofill { /* 去掉历史记录hover选中input中的背景颜色 */
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
  box-shadow: 0 0 0px 1000px white inset !important;
   /* font-size: 1.125rem !important; */
}
input[type=text]:-webkit-autofill::first-line {  /* 设置历史记录hover选中input中的字体大小, 参考地址 https://stackoverflow.com/questions/57195620/css-autocomplete-font-size*/
  font-size: 24px;
}

.bottom {
  display: flex;
  width: 100%;
}
.bottom label {
  width: 100%;
}
.bottom .login-btn {
  margin-left: auto;
}
.bottom .login-btn button {
  padding: 8px 14px 8px 14px;
  border: 1px solid #7e8993;
  outline: none;
  background:  white;
  border-radius: 4px;
  color: black;
}
.bottom .login-btn button:hover {
  transition-duration: 0.3s;
  border: 1px solid transparent;
  background: #FF6A00;
  color: white;
}
.bottom .login-btn button:active {
  background: rgba(246, 14, 6, 0.986);
}
.bottom input[type=checkbox] {
  border: 1px solid #7e8993;
  border-radius: 4px;
  line-height: 0;
  width: 1rem;
  height: 1rem;
  margin: -0.25rem 0.25rem 0 0;
  vertical-align: middle;
  -webkit-appearance: none;
  outline: none;
}

.bottom input[type=checkbox]:checked {
  content: url('../../static/img/icon/login-check.svg');
  border: 1px solid #FF6A00;
  outline: none;
}

.loading {
  display: flex;
  justify-content: center; /* flex 属性， 水平居中 */
  width: 100%;
  height: 50px;
}
</style>
